import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import * as firebase from 'firebase';
import 'firebase/firestore';

@Injectable()
export class ProfileProvider {
  db:any = firebase.firestore();
  basePath: string = '/uploads';
  userId: string = localStorage.getItem('userId');

  constructor(public http: HttpClient) {
    console.log('Hello ProfileProvider Provider', this.userId);
  }

  loadProfile() {
    const promise = new Promise((resolve, reject) => {
      this.db.collection('users').doc(this.userId).get().then(doc => {
        resolve(doc.data());
      }).catch(err => {
        reject(err);
      });
    });

    return promise;
  }

  editProfile(userData: object) {
    const promise = new Promise((resolve, reject) => {
      this.db.collection('users').doc(userData['uid']).update(userData).then(() => {
        resolve(1);
      }).catch(err => {
        reject(err);
      });
    });

     return promise;
  }

  uploadPhoto(upload) {
    const promise = new Promise((resolve, reject) => {
      let storageRef = firebase.storage().ref();
      let uploadTask = storageRef.child(`${this.basePath}/${upload.name}`).put(upload);

      uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
        (snapshot) => {
          upload.progress = (uploadTask.snapshot.bytesTransferred / uploadTask.snapshot.totalBytes) * 100
        },
        (error) => {
          reject(error);
        },
        () => {
          resolve(uploadTask.snapshot.downloadURL);
        }
      );
    });

    return promise;
  }

  saveFileData(uploadedFilePath) {
    const promise = new Promise((resolve, reject) => {
      this.db.collection('users').doc(this.userId).update({
        photo: uploadedFilePath
      }).then(() => {
        resolve(1);
      }).catch(err => {
        reject(err);
      });
    });

    return promise;
  }
}



